import PriceItem from '../../components/price-item.vue'

const defaultNum = 2

export default {
	components: { PriceItem },
	data() {
		return {
			goods: [ 0, 0 ],
			reset: true,
			cheapestIdx: -1,
		}
	},
	onLoad() {
		
	},
	methods: {
		addGood() {
			this.goods.push(0)
		},
		clear() {
			this.goods = [ 0, 0 ],
			this.reset = true
			this.cheapestIdx = -1
		},
		finishInput(idx, rate) {
			this.reset = false
			this.goods[idx] = rate
			this.cheapestIdx = this.getMaxIdx()
		},
		getMaxIdx() {
			let max = 0
			let maxIdx = -1
			this.goods.forEach((good, idx) => {
				if (good > max) {
					max = good
					maxIdx = idx
				}
			})
			return maxIdx
		},
	}
}
